"use client";

import { useEffect, useRef, useState, useCallback } from "react";
import { useParams } from "next/navigation";
import { fabric } from "fabric";
import io from "socket.io-client";
import Canvas from "@/components/Canvas";
import { Undo2, Redo2, Hand, ZoomIn, ZoomOut, Trash2 } from "lucide-react";

export default function Draw() {
    const { roomId } = useParams();
    const canvasRef = useRef<fabric.Canvas | null>(null);
    const videoRef = useRef<HTMLVideoElement>(null);
    const [aiEnabled, setAiEnabled] = useState(false);
    const [prompt, setPrompt] = useState("");
    const [history, setHistory] = useState<{ id: string; prompt_text: string; created_at: string }[]>([]);
    const [streamingEnabled, setStreamingEnabled] = useState(false);
    const [isDrawing, setIsDrawing] = useState(true);
    const [color, setColor] = useState("#000000");
    const [lineWidth, setLineWidth] = useState(3);
    const [undoStack, setUndoStack] = useState<fabric.Object[]>([]);
    const [redoStack, setRedoStack] = useState<fabric.Object[]>([]);
    const [scale, setScale] = useState(1);
    const [isPanning, setIsPanning] = useState(false);
    const [panOffset, setPanOffset] = useState({ x: 0, y: 0 });
    const [lastPanPoint, setLastPanPoint] = useState<{ x: number; y: number } | null>(null);
    const [socket, setSocket] = useState<SocketIOClient.Socket | null>(null);
    const [peerConnection, setPeerConnection] = useState<RTCPeerConnection | null>(null);
    const [error, setError] = useState<string | null>(null);

    // Initialize WebRTC
    useEffect(() => {
        if (streamingEnabled && roomId) {
            const socket = io(process.env.NEXT_PUBLIC_SIGNALING_URL || "http://localhost:3000");
            setSocket(socket);

            const pc = new RTCPeerConnection({
                iceServers: [{ urls: "stun:stun.l.google.com:19302" }],
            });
            setPeerConnection(pc);

            const stream = (canvasRef.current?.getElement() as HTMLCanvasElement)?.captureStream(30);
            if (stream) {
                stream.getTracks().forEach((track) => pc.addTrack(track, stream));
            }

            pc.onicecandidate = (event) => {
                if (event.candidate) {
                    socket.emit("ice-candidate", { sessionId: roomId, candidate: event.candidate });
                }
            };

            pc.createOffer()
                .then((offer) => pc.setLocalDescription(offer))
                .then(() => socket.emit("offer", { sessionId: roomId, offer: pc.localDescription }));

            socket.on("answer", (answer: RTCSessionDescriptionInit) => {
                pc.setRemoteDescription(new RTCSessionDescription(answer));
            });

            socket.on("ice-candidate", (candidate: RTCIceCandidateInit) => {
                pc.addIceCandidate(new RTCIceCandidate(candidate));
            });

            socket.emit("joinSession", roomId);

            return () => {
                pc.close();
                socket.disconnect();
            };
        }
    }, [streamingEnabled, roomId]);

    // Handle canvas transformations (pan/zoom)
    useEffect(() => {
        if (canvasRef.current) {
            canvasRef.current.setZoom(scale);
            canvasRef.current.setViewportTransform([scale, 0, 0, scale, panOffset.x, panOffset.y]);
            canvasRef.current.renderAll();
        }
    }, [scale, panOffset]);

    // Toggle draw/erase
    const toggleDrawErase = () => {
        if (canvasRef.current) {
            canvasRef.current.isDrawingMode = !isDrawing;
            canvasRef.current.freeDrawingBrush = isDrawing
                ? new fabric.EraserBrush(canvasRef.current)
                : new fabric.PencilBrush(canvasRef.current);
            canvasRef.current.freeDrawingBrush.width = lineWidth;
            canvasRef.current.freeDrawingBrush.color = isDrawing ? "white" : color;
            setIsDrawing(!isDrawing);
        }
    };

    // Clear canvas
    const clearCanvas = () => {
        if (canvasRef.current) {
            canvasRef.current.clear();
            setUndoStack([]);
            setRedoStack([]);
        }
    };

    // Undo
    const undo = () => {
        if (canvasRef.current) {
            const objects = canvasRef.current.getObjects();
            if (objects.length > 0) {
                const lastObject = objects[objects.length - 1];
                canvasRef.current.remove(lastObject);
                setUndoStack((prev) => prev.slice(0, -1));
                setRedoStack((prev) => [...prev, lastObject]);
            }
        }
    };

    // Redo
    const redo = () => {
        if (canvasRef.current && redoStack.length > 0) {
            const lastObject = redoStack[redoStack.length - 1];
            canvasRef.current.add(lastObject);
            setRedoStack((prev) => prev.slice(0, -1));
            setUndoStack((prev) => [...prev, lastObject]);
        }
    };

    // Zoom in/out
    const zoomIn = () => setScale((prev) => Math.min(prev + 0.1, 3));
    const zoomOut = () => setScale((prev) => Math.max(prev - 0.1, 0.5));

    // Pan mode
    const togglePanMode = () => {
        setIsPanning((prev) => !prev);
        if (isPanning) setLastPanPoint(null);
    };

    // Enhance with AI
    const handleEnhance = async (promptText?: string) => {
        if (!aiEnabled) return;
        const canvasData = canvasRef.current?.toDataURL("image/png");
        try {
            setError(null);
            const response = await fetch("/api/enhance", {
                method: "POST",
                headers: { "Content-Type": "application/json" },
                body: JSON.stringify({ image: canvasData, prompt: promptText || prompt }),
            });
            if (!response.ok) throw new Error("AI enhancement failed");
            const { enhancedImage } = await response.json();
            canvasRef.current?.loadFromJSON(enhancedImage, () => canvasRef.current?.renderAll());
            if (promptText || prompt) {
                setHistory((prev) => [
                    { id: Math.random().toString(), prompt_text: promptText || prompt, created_at: new Date().toISOString() },
                    ...prev,
                ]);
                setPrompt("");
            }
        } catch (error) {
            console.error("AI enhancement failed:", error);
            setError("Failed to enhance with AI. Please try again.");
        }
    };

    return (
        <div className="relative w-screen h-screen overflow-hidden bg-neutral-100">
            {error && (
                <div className="absolute top-16 left-0 right-0 bg-red-500 text-white text-center p-2 z-30">
                    {error}
                </div>
            )}
            <div className="absolute top-0 left-0 right-0 p-4 flex items-center justify-between bg-white shadow-sm z-20">
                <h1 className="text-base font-semibold">Bezalel Draw - Session {roomId}</h1>
                <div className="flex gap-2 items-center">
                    <label className="flex items-center gap-2">
                        <input
                            type="checkbox"
                            checked={aiEnabled}
                            onChange={() => setAiEnabled(!aiEnabled)}
                        />
                        Enable AI Enhancement
                    </label>
                    <label className="flex items-center gap-2">
                        <input
                            type="checkbox"
                            checked={streamingEnabled}
                            onChange={() => setStreamingEnabled(!streamingEnabled)}
                        />
                        Enable Streaming
                    </label>
                </div>
            </div>
            <div className="flex w-full h-full pt-16">
                <div className="flex-1 relative bg-neutral-200 overflow-auto">
                    <div className="w-[3000px] h-[3000px] flex items-center justify-center">
                        <Canvas
                            isDrawingMode={isDrawing}
                            setCanvasRef={(canvas) => (canvasRef.current = canvas)}
                            width={1600}
                            height={1200}
                            isPanning={isPanning}
                            onPanMove={(e, lastPoint) => {
                                if (lastPoint && isPanning) {
                                    const dx = e.clientX - lastPoint.x;
                                    const dy = e.clientY - lastPoint.y;
                                    setPanOffset((prev) => ({ x: prev.x + dx, y: prev.y + dy }));
                                    setLastPanPoint({ x: e.clientX, y: e.clientY });
                                }
                            }}
                            onPanStart={(e) => setLastPanPoint({ x: e.clientX, y: e.clientY })}
                            onPanEnd={() => setLastPanPoint(null)}
                            onPathCreated={(path) => setUndoStack((prev) => [...prev, path])}
                        />
                    </div>
                </div>
                <aside className="w-80 p-4 border-l bg-white overflow-y-auto">
                    <h3 className="font-medium text-gray-800">AI Prompts</h3>
                    <div className="mt-2">
                        <input
                            value={prompt}
                            onChange={(e) => setPrompt(e.target.value)}
                            placeholder="Enter AI style (e.g., Van Gogh)"
                            className="w-full border p-2 rounded text-sm text-gray-900 bg-white"
                        />
                        <button
                            onClick={() => handleEnhance()}
                            disabled={!aiEnabled || !prompt}
                            className="mt-2 px-3 py-1 bg-blue-600 text-white rounded text-sm disabled:opacity-50"
                        >
                            Enhance
                        </button>
                    </div>
                    <div className="mt-4">
                        <h4 className="font-medium">Prompt History</h4>
                        <ul className="mt-2 space-y-1 max-h-56 overflow-auto text-sm">
                            {history.map((h) => (
                                <li key={h.id} className="flex justify-between items-center">
                                    <span className="truncate mr-2">{h.prompt_text}</span>
                                    <button
                                        onClick={() => handleEnhance(h.prompt_text)}
                                        className="text-blue-600 text-xs underline"
                                    >
                                        Reapply
                                    </button>
                                </li>
                            ))}
                        </ul>
                    </div>
                </aside>
            </div>
            <div className="absolute bottom-4 left-1/2 -translate-x-1/2 bg-neutral-900 text-white rounded-2xl shadow-lg px-4 py-2 flex items-center gap-4 z-30">
                <button onClick={undo} className="hover:bg-neutral-700 p-2 rounded">
                    <Undo2 size={18} />
                </button>
                <button onClick={redo} className="hover:bg-neutral-700 p-2 rounded">
                    <Redo2 size={18} />
                </button>
                <button
                    onClick={togglePanMode}
                    className={`hover:bg-neutral-700 p-2 rounded ${isPanning ? "bg-neutral-700" : ""}`}
                >
                    <Hand size={18} />
                </button>
                <button onClick={zoomIn} className="hover:bg-neutral-700 p-2 rounded">
                    <ZoomIn size={18} />
                </button>
                <button onClick={zoomOut} className="hover:bg-neutral-700 p-2 rounded">
                    <ZoomOut size={18} />
                </button>
                <input
                    type="color"
                    value={color}
                    onChange={(e) => setColor(e.target.value)}
                    className="w-7 h-7 rounded-full border cursor-pointer"
                />
                <input
                    type="range"
                    min={1}
                    max={20}
                    value={lineWidth}
                    onChange={(e) => setLineWidth(Number(e.target.value))}
                    className="w-24 cursor-pointer"
                />
                <button onClick={clearCanvas} className="hover:bg-neutral-700 p-2 rounded">
                    <Trash2 size={18} />
                </button>
            </div>
            {streamingEnabled && <video ref={videoRef} autoPlay muted className="hidden" />}
        </div>
    );
}