"use client";

import { useEffect, useRef } from "react";
import { fabric } from "fabric";

interface CanvasProps {
    isDrawingMode: boolean;
    setCanvasRef?: (canvas: fabric.Canvas | null) => void;
    isPanning?: boolean;
    onPanStart?: (e: React.MouseEvent<HTMLCanvasElement>) => void;
    onPanMove?: (e: React.MouseEvent<HTMLCanvasElement>, lastPoint: { x: number; y: number } | null) => void;
    onPanEnd?: () => void;
    onPathCreated?: (path: fabric.Path) => void;
    width?: number;
    height?: number;
}

export default function Canvas({
    isDrawingMode,
    setCanvasRef,
    isPanning,
    onPanStart,
    onPanMove,
    onPanEnd,
    onPathCreated,
    width = 1600,
    height = 1200,
}: CanvasProps) {
    const canvasRef = useRef<HTMLCanvasElement>(null);
    const fabricRef = useRef<fabric.Canvas | null>(null);

    useEffect(() => {
        fabricRef.current = new fabric.Canvas(canvasRef.current, {
            isDrawingMode,
            width,
            height,
            freeDrawingBrush: isDrawingMode ? new fabric.PencilBrush() : undefined,
        });

        if (isDrawingMode) {
            fabricRef.current.freeDrawingBrush.width = 3;
            fabricRef.current.freeDrawingBrush.color = "#000000";
        }

        if (onPathCreated) {
            fabricRef.current.on("path:created", ({ path }) => onPathCreated(path));
        }

        if (setCanvasRef) {
            setCanvasRef(fabricRef.current);
        }

        return () => {
            fabricRef.current?.dispose();
            if (setCanvasRef) setCanvasRef(null);
        };
    }, [isDrawingMode, setCanvasRef, onPathCreated, width, height]);

    return (
        <canvas
            ref={canvasRef}
            className="bg-white shadow-md rounded border"
            onMouseDown={isPanning ? onPanStart : undefined}
            onMouseMove={isPanning ? (e) => onPanMove?.(e, lastPanPoint) : undefined}
            onMouseUp={isPanning ? onPanEnd : undefined}
            onMouseLeave={isPanning ? onPanEnd : undefined}
        />
    );
}